import React from 'react';
import { Link } from 'react-router-dom';
import { TTrainerCaseStatus } from '../types';
import { trainerCaseStatus } from '../utils';
import { Icon } from './Icon';
import { PuzzlePreview } from './PuzzlePreview';

type TProps = {
    ariaLabel?: string;
    cacheRequests?: boolean;
    callback?: (...args: unknown[]) => unknown;
    href?: string;
    name?: string;
    position?: number;
    puzzlePreviewHash?: string;
    selected?: boolean;
    source: string;
    status?: TTrainerCaseStatus;
    tooltip?: string;
};

export function CasePreview ({
    ariaLabel,
    cacheRequests,
    callback,
    href,
    name,
    position,
    puzzlePreviewHash,
    selected,
    source,
    status,
    tooltip
}: TProps) {
    const className = 'case-preview';
    const classNames = [className];

    if (selected === false) {
        classNames.push(`${className}--not-selected`);
    }

    if (typeof(status) === 'number') {
        classNames.push(`${className}--color-${trainerCaseStatus[status].color}`);
    }

    function renderHeader () {
        const statusElement = typeof(status) === 'number' ? (
            <p className={`${className}__status`}>
                <Icon
                    ariaLabel={trainerCaseStatus[status].label}
                    name={trainerCaseStatus[status].iconName}
                    parentClass={`${className}__status-icon`}
                />
            </p>
        ) : null;

        const positionElement = typeof(position) === 'number' ? (
            <p className={`${className}__position`}>{position}</p>
        ) : null;

        return statusElement || positionElement ? (
            <div className={`${className}__header`}>
                {statusElement}
                {positionElement}
            </div>
        ) : null;
    }

    function renderPuzzlePreview () {
        if (!source) {
            return null;
        }

        return (
            <div className={`${className}__puzzle-preview`}>
                <PuzzlePreview
                    cacheRequests={cacheRequests}
                    hash={puzzlePreviewHash}
                    source={source}
                />
            </div>
        );
    }

    function renderFooter () {
        const nameElement = name ? (
            <p className={`${className}__name`}>
                {name}
            </p>
        ) : null;

        return nameElement ? (
            <div className={`${className}__footer`}>
                {nameElement}
            </div>
        ) : null;
    }

    return href ? (
        <Link
            aria-label={ariaLabel || tooltip}
            className={classNames.join(' ')}
            data-tooltip={tooltip}
            data-tooltip-position='center'
            to={href}
        >
            {renderHeader()}
            {renderPuzzlePreview()}
            {renderFooter()}
        </Link>
    ) : (
        <button
            aria-label={ariaLabel || tooltip}
            aria-selected={selected}
            className={classNames.join(' ')}
            data-tooltip={tooltip}
            data-tooltip-position='center'
            onClick={callback}
            type='button'
        >
            {renderHeader()}
            {renderPuzzlePreview()}
            {renderFooter()}
        </button>
    );
}