import React, { ReactNode, useState } from 'react';
import { getRandomInteger } from '../utils';
import { Icon } from './Icon';
import { Separator } from './Separator';

type TProps = {
    alignment?: 'left' | 'center' | 'right';
    anchorId?: string;
    children?: ReactNode;
    openByDefault?: boolean;
    title: string;
    tooltip?: string;
};

export function Accordion ({ alignment, anchorId, children, openByDefault = false, title, tooltip }: TProps) {
    const className = 'accordion';
    const classNames = [className];

    const [open, setOpen] = useState(openByDefault);

    if (alignment) {
        classNames.push(`${className}--alignment-${alignment}`);
    }

    const contentId = `accordion-content-${getRandomInteger(100000, 999999)}`;

    if (open) {
        classNames.push(`${className}--open`);
    }

    return (
        <div className={classNames.join(' ')}>
            <button
                aria-label={open ? 'Close accordion' : 'Open accordion'}
                aria-controls={contentId}
                aria-expanded={open}
                className={`${className}__button`}
                data-tooltip={tooltip}
                onClick={() => setOpen(!open)}
            >
                <h2
                    className={`${className}__title`}
                    id={anchorId}
                >
                    {title}
                </h2>
                <Icon name={open ? 'minus-circle' : 'plus-circle'} parentClass={`${className}__icon`} />
            </button>
            <div
                className={`${className}__content`}
                id={contentId}
                // @ts-expect-error (React issue)
                inert={!open ? '' : null}
            >
                <Separator />
                <div className={`${className}__content-inner`}>
                    {children}
                </div>
            </div>
        </div>
    );
}