import React, { Fragment, ReactNode } from 'react';
import { Loader } from './Loader';

type TProps = {
    children: ReactNode;
    loading?: boolean;
    title?: string;
};

export function Page ({ children, loading, title }: TProps) {
    const className = 'page';

    return (
        <div className={className}>
            <main className={`${className}__content`}>
                {
                    loading ? (
                        <Fragment>
                            <h1 className={`${className}__title`}>Loading...</h1>
                            <div className={`${className}__loader`}>
                                <Loader />
                            </div>
                        </Fragment>
                    ) : (
                        <Fragment>
                            { title && <h1 className={`${className}__title`}>{title}</h1> }
                            {children}
                        </Fragment>
                    )
                }
            </main>
        </div>
    );
}