/** Capitalize each word of a sentence. */
export function capitalize (sentence: string) {
    if (!sentence) {
        throw new Error('Missing sentence.');
    }

    const splitSentence = sentence.split(' ').map((word) => {
        return word.charAt(0).toUpperCase() + word.slice(1);
    });

    return splitSentence.join(' ');
}

/** Get a random interger between two intergers (inclusive). */
export function getRandomInteger (min: number, max: number) {
    if (typeof(min) !== 'number') {
        throw new Error('Missing min.');
    }

    if (typeof(max) !== 'number') {
        throw new Error('Missing max.');
    }

    return Math.floor(Math.random() * (Math.floor(max) - Math.ceil(min) + 1)) + Math.ceil(min);
}

/** Format milliseconds to hours, minutes, seconds and milliseconds. */
export function formatTime (time: number) {
    let formatedTime = '';

    const date = new Date(Date.UTC(0, 0, 0, 0, 0, 0, time));

    const milliseconds = Math.floor(date.getUTCMilliseconds());
    const seconds = date.getUTCSeconds();
    const minutes = date.getUTCMinutes();
    const hours = date.getUTCHours();

    let formatedMilliseconds = milliseconds.toString();
    let formatedSeconds = seconds.toString();
    let formatedMinutes = minutes.toString();
    const formatedHours = hours.toString();

    // Prefix milliseconds
    if (milliseconds < 10) {
        formatedMilliseconds = '00' + milliseconds;
    } else if (milliseconds < 100) {
        formatedMilliseconds = '0' + milliseconds;
    }

    // Prefix seconds
    if (minutes > 0 && seconds < 10) {
        formatedSeconds = '0' + seconds;
    }

    // Prefix minutes
    if (hours > 0 && minutes < 10) {
        formatedMinutes = '0' + minutes;
    }

    if (hours) {
        formatedTime = `${formatedHours}:${formatedMinutes}:${formatedSeconds}.${formatedMilliseconds}`;
    } else if (minutes) {
        formatedTime = `${formatedMinutes}:${formatedSeconds}.${formatedMilliseconds}`;
    } else {
        formatedTime = `${formatedSeconds}.${formatedMilliseconds}`;
    }

    return formatedTime;
}

/**
 * Parse a time string into milliseconds.
 *
 * Format hhmmssmls
 * */
export function parseTime (time: string) {
    const regex = new RegExp('^[0-9]{1,9}$');

    if (!time || !regex.test(time)) {
        return null;
    }

    const formattedTime = time.padStart(9, '0');

    const milliseconds = Number(formattedTime.slice(-3));
    const seconds = Number(formattedTime.slice(-5, -3));
    const minutes = Number(formattedTime.slice(-7, -5));
    const hours = Number(formattedTime.slice(-formattedTime.length, -7));

    return (milliseconds + (seconds * 1000) + (minutes * 60 * 1000) + (hours * 60 * 60 * 1000));
}

/** Disable scroll on the body. */
export function disableBodyScroll () {
    document.body.style.overflow = 'hidden';
}

/** Enable scroll on the body. */
export function enableBodyScroll () {
    document.body.style.overflow = 'initial';
}

/** Query all focusable elements inside a container */
export function queryAllFocusableElements (containerElement: HTMLElement) {
    if (!containerElement) {
        throw new Error('Missing container element.');
    }

    const selector = `
        a[href],
        area[href],
        input:not([disabled]),
        select:not([disabled]),
        textarea:not([disabled]),
        button:not([disabled]),
        iframe,
        object,
        embed,
        *[tabindex],
        *[contenteditable]
    `;

    return containerElement.querySelectorAll(selector) as NodeListOf<HTMLElement>;
}

/** Trap focus inside a container. */
export function trapFocus (containerElement: HTMLElement) {
    if (!containerElement) {
        throw new Error('Missing container element.');
    }

    const focusableElements = queryAllFocusableElements(containerElement);

    if (focusableElements.length === 0) {
        return;
    }

    const firstElement = focusableElements[0];
    const lastElement = focusableElements[focusableElements.length - 1];

    firstElement.addEventListener('keydown', (event) => {
        if (
            event.key === 'Tab'
            && !event.ctrlKey
            && !event.altKey
            && !event.metaKey
            && event.shiftKey
        ) {
            event.preventDefault();
            lastElement.focus();
        }
    });

    lastElement.addEventListener('keydown', (event) => {
        if (
            event.key === 'Tab'
            && !event.ctrlKey
            && !event.altKey
            && !event.metaKey
            && !event.shiftKey
        ) {
            event.preventDefault();
            firstElement.focus();
        }
    });
}

// ----- Array -----

/** Get a random item from an array. */
export function getRandomItemFromArray (array: any[]) {
    if (!array) {
        throw new Error('Missing array.');
    }

    return array[getRandomInteger(0, array.length - 1)];
}

/** Shuffle an array using the FisherYates method. */
export function shuffleArray (array: any[]) {
    if (!array) {
        throw new Error('Missing array.');
    }

    const newArray = array;
    let currentIndex = newArray.length;

    // While there remain elements to shuffle
    while (currentIndex != 0) {
        // Pick a remaining element
        const randomIndex = Math.floor(Math.random() * currentIndex);
        currentIndex--;

        // And swap it with the current element.
        [newArray[currentIndex], newArray[randomIndex]] = [newArray[randomIndex], newArray[currentIndex]];
    }

    return newArray;
}

// ----- Custom Properties -----

/** Get custom property value from :root. */
export function getCustomPropertyValue (name: string) {
    if (!name) {
        throw new Error('Missing name.');
    }

    return getComputedStyle(document.documentElement).getPropertyValue(name);
}

/** Set custom property value on :root. */
export function setCustomPropertyValue (name: string, value: string) {
    if (!name) {
        throw new Error('Missing name.');
    }

    if (!value) {
        throw new Error('Missing value.');
    }

    document.documentElement.style.setProperty(name, value);
}

/** Get longhand form of hex color. */
export function getHexColorLonghand (color: string) {
    if (!color) {
        throw new Error('Missing color.');
    }

    if (!color.match(/^#(?:[0-9a-fA-F]{3}){1,2}$/)) {
        throw new Error('Invalid color.');
    }

    if (color.length === 4) {
        return '#' + color[1] + color[1] + color[2] + color[2] + color[3] + color[3];
    } else {
        return color;
    }
}

// ----- Puzzles -----
export function updateColorSchemeCustomProperties (colorScheme: TPuzzleColorScheme) {
    if (!colorScheme) {
        throw new Error('Missing color scheme.');
    }

    Object.keys(colorScheme).forEach((colorName) => {
        setCustomPropertyValue(colorName, colorScheme[colorName]);
    });
}

// ----- Trainer -----

/** Fetch trainer from JSON file. */
export async function fetchTrainer (puzzleName: TPuzzleName, trainerId: string) {
    if (!puzzleName) {
        throw new Error('Missing puzzle name.');
    }

    if (!trainerId) {
        throw new Error('Missing trainer id.');
    }

    try {
        const response = await fetch(`/trainers/${puzzleName}/${trainerId}/data.json`);
        const trainer = await response.json() as TTrainer;

        const trainerCasesId = Object.keys(trainer.cases);

        const storeTrainerData = storeManager.getTrainer(puzzleName, trainerId, trainerCasesId);

        const mergedTrainer: TTrainer = merge({}, trainer, storeTrainerData);

        return mergedTrainer;
    } catch (error) {
        throw new Error(error.message);
    }
}

/** Get sets. */
export function getSets (trainer: TTrainer, filterDeleted = true) {
    if (!trainer) {
        throw new Error('Missing trainer.');
    }

    let trainerSets: TTrainerSet[] = [];

    if (filterDeleted) {
        // Gets sets that aren't deleted
        trainerSets = Object.values(trainer.sets).filter((trainerSet) => trainerSet.deleted !== true);
    } else {
        // Get all sets
        trainerSets = Object.values(trainer.sets);
    }

    // Sort sets by position
    trainerSets.sort((firstSet, secondSet) => {
        if (firstSet.position < secondSet.position) {
            return -1;
        } else if (firstSet.position > secondSet.position) {
            return 1;
        }

        return 0;
    });

    return trainerSets;
}

/** Get cases. */
export function getCases (trainer: TTrainer, filterDeleted = true) {
    if (!trainer) {
        throw new Error('Missing trainer.');
    }

    let trainerCases: TTrainerCase[] = [];

    if (filterDeleted) {
        // Gets cases that aren't deleted
        trainerCases = Object.values(trainer.cases).filter((trainerCase) => trainerCase.deleted !== true);
    } else {
        // Get all cases
        trainerCases = Object.values(trainer.cases);
    }

    // Sort cases
    trainerCases.sort((firstCase, secondCase) => {
        // Get set positions
        const firstCaseSetPosition = trainer.sets[firstCase.setId].position;
        const secondCaseSetPosition = trainer.sets[secondCase.setId].position;

        // Sort by set position
        if (firstCaseSetPosition < secondCaseSetPosition) {
            return -1;
        } else if (firstCaseSetPosition > secondCaseSetPosition) {
            return 1;
        }

        // Sort by case position
        if (firstCase.position < secondCase.position) {
            return -1;
        } else if (firstCase.position > secondCase.position) {
            return 1;
        }

        // Default
        return 0;
    });

    return trainerCases;
}

/** Get selected cases. */
export function getCasesSelected (trainer: TTrainer, filterDeleted = true) {
    if (!trainer) {
        throw new Error('Missing trainer.');
    }

    const filteredCases = getCases(trainer, filterDeleted).filter((trainerCase) => {
        return trainerCase.selected;
    });

    return filteredCases;
}

/** Get cases with status. */
export function getCasesWithStatus (trainer: TTrainer, status: TTrainerCaseStatus, filterDeleted = true) {
    if (!trainer) {
        throw new Error('Missing trainer.');
    }

    if (typeof(status) !== 'number') {
        throw new Error('Missing status.');
    }

    const filteredCases = getCases(trainer, filterDeleted).filter((trainerCase) => {
        return trainerCase.status === status;
    });

    return filteredCases;
}

/** Get cases from set. */
export function getCasesFromSet (trainer: TTrainer, setId: string, filterDeleted = true) {
    if (!trainer) {
        throw new Error('Missing trainer.');
    }

    if (!setId) {
        throw new Error('Missing set id.');
    }

    const filteredCases = getCases(trainer, filterDeleted).filter((trainerCase) => {
        return trainerCase.setId === setId;
    });

    return filteredCases;
}

/** Get selected cases from set. */
export function getCasesFromSetSelected (trainer: TTrainer, setId: string, filterDeleted = true) {
    if (!trainer) {
        throw new Error('Missing trainer.');
    }

    if (!setId) {
        throw new Error('Missing set id.');
    }

    const filteredCases = getCases(trainer, filterDeleted).filter((trainerCase) => {
        return trainerCase.setId === setId && trainerCase.selected;
    });

    return filteredCases;
}

/** Get cases from set with status. */
export function getCasesFromSetWithStatus (trainer: TTrainer, setId: string, status: TTrainerCaseStatus, filterDeleted = true) {
    if (!trainer) {
        throw new Error('Missing trainer.');
    }

    if (!setId) {
        throw new Error('Missing set id.');
    }

    const filteredCases = getCases(trainer, filterDeleted).filter((trainerCase) => {
        return trainerCase.setId === setId && trainerCase.status === status;
    });

    return filteredCases;
}

/** Get average of all times from case. */
export function getAverageTime (times: TTrainerCaseTime[]) {
    if (!times) {
        throw new Error('Missing times.');
    }

    if (!times || times.length === 0) {
        return null;
    }

    let sum = 0;
    let total = 0;

    times.forEach((time) => {
        const { penalty, milliseconds } = time;

        switch (penalty) {
            case '+2':
                sum += milliseconds + 2000;
                total++;
                break;
            case 'dnf':
                break;
            default:
                sum += milliseconds;
                total++;
                break;
        }
    });

    return sum / total;
}

/** Get best time from case. */
export function getBestTime (times: TTrainerCaseTime[]): TTrainerCaseTime | null {
    if (!times) {
        throw new Error('Missing times.');
    }

    if (!times || times.length === 0) {
        return null;
    }

    let best: TTrainerCaseTime | null = null;

    times.forEach((time) => {
        if (time.penalty === 'dnf') {
            return;
        }

        if (best === null) {
            best = time;
        }

        const bestMilliseconds = best.penalty === '+2' ? best.milliseconds + 2000 : best.milliseconds;
        const timeMilliseconds = time.penalty === '+2' ? time.milliseconds + 2000 : time.milliseconds;

        if (timeMilliseconds < bestMilliseconds) {
            best = time;
        }
    });

    return best;
}

/** Get worst time from case. */
export function getWorstTime (times: TTrainerCaseTime[]): TTrainerCaseTime | null {
    if (!times) {
        throw new Error('Missing times.');
    }

    if (!times || times.length === 0) {
        return null;
    }

    let worst: TTrainerCaseTime | null = null;

    times.forEach((time) => {
        if (time.penalty === 'dnf') {
            return;
        }

        if (worst === null) {
            worst = time;
        }

        const worstMilliseconds = worst.penalty === '+2' ? worst.milliseconds + 2000 : worst.milliseconds;
        const timeMilliseconds = time.penalty === '+2' ? time.milliseconds + 2000 : time.milliseconds;

        if (timeMilliseconds > worstMilliseconds) {
            worst = time;
        }
    });

    return worst;
}

/** Get +2 times from case. */
export function getPlusTwoTimes (times: TTrainerCaseTime[]) {
    if (!times) {
        throw new Error('Missing times.');
    }

    if (!times || times.length === 0) {
        return null;
    }

    return times.filter((time) => time.penalty === '+2');
}

/** Get DNF times from case. */
export function getDnfTimes (times: TTrainerCaseTime[]) {
    if (!times) {
        throw new Error('Missing times.');
    }

    if (!times || times.length === 0) {
        return null;
    }

    return times.filter((time) => time.penalty === 'dnf');
}

/** Get case image source. */
export function getCaseImageSource (puzzleName: TPuzzleName, trainerId: string, caseId: string) {
    if (!puzzleName) {
        throw new Error('Missing puzzle name.');
    }

    if (!trainerId) {
        throw new Error('Missing trainer id.');
    }

    if (!caseId) {
        throw new Error('Missing case id.');
    }

    return `/trainers/${puzzleName}/${trainerId}/images/${caseId}.svg`;
}

/** Get completion percentage */
export function getCompletionPercentage (trainer: TTrainer) {
    if (!trainer) {
        throw new Error('Missing trainer.');
    }

    const numberOfCases = getCases(trainer).length;
    const numberOfCasesWithStatusTwo = getCasesWithStatus(trainer, 2).length;

    return Math.ceil((numberOfCasesWithStatusTwo * 100) / numberOfCases);
}