// https://github.com/gilbarbara/react-inlinesvg

import React, { RefObject, useRef } from 'react';
import Svg from 'react-inlinesvg';
import { Icon } from './Icon';
import { Loader } from './Loader';

type TProps = {
    cacheRequests?: boolean;
    hash?: string;
    onErrorCallback?: (error: Error) => unknown;
    onLoadCallback?: (ref: RefObject<SVGElement>) => unknown;
    source: string;
};

export function PuzzlePreview ({ cacheRequests = true, hash, onErrorCallback, onLoadCallback, source }: TProps) {
    const className = 'puzzle-preview';

    const ref = useRef<SVGElement>(null);

    const loaderElement = (
        <div className={`${className}__loader`}>
            <Loader />
        </div>
    );

    return (
        <div className={className}>
            <Svg
                cacheRequests={cacheRequests}
                className={`${className}__svg`}
                innerRef={ref}
                key={hash}
                loader={loaderElement}
                onError={(error) => onErrorCallback && onErrorCallback(error)}
                onLoad={() => onLoadCallback && onLoadCallback(ref)}
                src={source}
                description='Puzzle preview'
            >
                <div
                    className={`${className}__error`}
                    data-tooltip='Image could not be loaded'
                    data-tooltip-position='center'
                    tabIndex={0}
                >
                    <Icon name='x' parentClass={`${className}__icon`} />
                </div>
            </Svg>
        </div>
    );
};