import React, { ReactNode } from 'react';
import { List } from './List';

type TProps = {
    children: ReactNode;
    marginLeftIndex?: number;
};

export function Header ({ children, marginLeftIndex }: TProps) {
    const className = 'header';

    if (!children) {
        return null;
    }

    return (
        <header className={className}>
            <nav className={`${className}__navigation`} aria-label='Main navigation'>
                <List gap={15} marginLeftIndex={marginLeftIndex}>
                    {children}
                </List>
            </nav>
        </header>
    );
}