import React, { forwardRef } from 'react';
import { Link } from 'react-router-dom';
import { TTooltipPosition } from '../types';
import { Icon } from './Icon';

type TProps = {
    callback?: (...args: unknown[]) => unknown;
    href?: string;
    iconName: string;
    tooltip: string;
    tooltipPosition?: TTooltipPosition;
};

export const ButtonRound = forwardRef((
    { callback, href, iconName, tooltip, tooltipPosition }: TProps,
    ref: any
) => {
    const className = 'button-round';

    const iconElement = <Icon name={iconName} parentClass={`${className}__icon`} />;

    return href ? (
        <Link
            aria-label={tooltip}
            className={className}
            data-tooltip={tooltip}
            data-tooltip-position={tooltipPosition}
            ref={ref}
            to={href}
        >
            {iconElement}
        </Link>
    ) : (
        <button
            aria-label={tooltip}
            className={className}
            data-tooltip={tooltip}
            data-tooltip-position={tooltipPosition}
            onClick={callback}
            ref={ref}
            type='button'
        >
            {iconElement}
        </button>
    );
});