import React, { ReactNode } from 'react';

type TProps = {
    alignment?: 'left' | 'center' | 'right';
    children?: ReactNode;
    gap?: number;
    marginLeftIndex?: number;
};

export function List ({ alignment, children, gap = 10, marginLeftIndex }: TProps) {
    const className = 'list';
    const classNames = [className];

    const itemClassName = `${className}__item`;

    if (alignment) {
        classNames.push(`${className}--alignment-${alignment}`);
    }

    function renderItems () {
        if (!children) {
            return null;
        }

        // If there are multiple children
        if (children[Symbol.iterator]) {
            return (children as ReactNode[]).map((child, index) => {
                const itemClassNames = [itemClassName];

                if (index === marginLeftIndex) {
                    itemClassNames.push(`${itemClassName}--margin-left`);
                }

                return child ? (
                    <li className={itemClassNames.join(' ')} key={`item-${index}`}>
                        {child}
                    </li>
                ) : null;
            });
        } else {
            const itemClassNames = [itemClassName];

            if (marginLeftIndex === 0) {
                itemClassNames.push(`${itemClassName}--margin-left`);
            }

            return (
                <div className={itemClassNames.join(' ')}>
                    {children}
                </div>
            );
        }
    }

    if (!children) {
        return null;
    }

    return children[Symbol.iterator] ? (
        <ul
            className={classNames.join(' ')}
            style={{ gap }}
        >
            {renderItems()}
        </ul>
    ) : (
        <div className={classNames.join(' ')}>
            {renderItems()}
        </div>
    );
}