import React from 'react';
import { Link } from 'react-router-dom';
import { TTooltipPosition } from '../types';
import { Icon } from './Icon';

type TProps = {
    ariaLabel?: string;
    callback?: (...args: unknown[]) => unknown;
    color?: 'discord' | 'green' | 'grey' | 'grey-super-light' | 'orange' | 'red';
    href?: string;
    iconName?: string;
    label?: string;
    size?: 'small' | 'medium';
    target?: '_blank' | '_self';
    tooltip?: string;
    tooltipPosition?: TTooltipPosition;
};

export function Button ({
    ariaLabel,
    callback,
    color = 'grey',
    href,
    iconName,
    label,
    size = 'medium',
    target = '_self',
    tooltip,
    tooltipPosition
}: TProps) {
    const className = 'button';
    const classNames = [className];

    classNames.push(`${className}--color-${color}`);
    classNames.push(`${className}--size-${size}`);

    const labelElement = label && <span className={`${className}__label`}>{label}</span>;
    const iconELement = iconName && <Icon name={iconName} parentClass={`${className}__icon`} />;

    return href ? (
        <Link
            aria-label={ariaLabel || tooltip}
            className={classNames.join(' ')}
            data-tooltip={tooltip}
            data-tooltip-position={tooltipPosition}
            target={target}
            to={href}
        >
            {labelElement}
            {iconELement}
        </Link>
    ) : (
        <button
            aria-label={ariaLabel || tooltip}
            className={classNames.join(' ')}
            data-tooltip={tooltip}
            data-tooltip-position={tooltipPosition}
            onClick={callback}
            type='button'
        >
            {labelElement}
            {iconELement}
        </button>
    );
}