import React, { Component, ReactNode } from 'react';
import { version } from '../../../package.json';
import { Accordion } from './Accordion';
import { Button } from './Button';
import { ButtonRound } from './ButtonRound';
import { Group } from './Group';
import { Header } from './Header';
import { Page } from './Page';

type TProps = {
    children: ReactNode;
};

type TState = {
    error: Error | null;
};

export class ErrorBoundary extends Component<TProps, TState> {
    className: string;
    homeUrl: string;
    state: TState;

    constructor (props: TProps) {
        super(props);
        this.state = { error: null };
        this.className = 'error-boundary';
        this.homeUrl = '/';

        this.promiseRejectionHandler = this.promiseRejectionHandler.bind(this);
        this.keydownHandler = this.keydownHandler.bind(this);
    }

    static getDerivedStateFromError (error: Error) {
        return { error };
    }

    componentDidMount () {
        window.addEventListener('unhandledrejection', this.promiseRejectionHandler);
        document.addEventListener('keydown', this.keydownHandler);
    }

    componentWillUnmount () {
        window.removeEventListener('unhandledrejection', this.promiseRejectionHandler);
        document.removeEventListener('keydown', this.keydownHandler);
    }

    promiseRejectionHandler (event: PromiseRejectionEvent) {
        this.setState({
            error: event.reason
        });
    }

    keydownHandler (event: KeyboardEvent) {
        const { homeUrl } = this;
        const { error } = this.state;

        if (
            event.altKey
            || event.ctrlKey
            || event.metaKey
            || event.shiftKey
            || !error
        ) {
            return;
        }

        // H
        if (event.code === 'KeyH') {
            event.preventDefault();
            window.location.href = homeUrl;
        }
    }

    renderHeader () {
        const { homeUrl } = this;

        return (
            <Header>
                {/* Home */}
                <ButtonRound
                    href={homeUrl}
                    iconName='house'
                    tooltip='Home (H)'
                />
                {/* Reload */}
                <ButtonRound
                    callback={() => window.location.reload()}
                    iconName='arrow-clockwise'
                    tooltip='Reload page (F5)'
                />
            </Header>
        );
    }

    render () {
        const { className } = this;
        const { children } = this.props;
        const { error } = this.state;

        if (!error) {
            return children;
        }

        return (
            <Page>
                <div className={className}>
                    {this.renderHeader()}
                    <img className={`${className}__logo`} src="/assets/images/logo.svg" alt="Logo" />
                    <Group alignment='center' title='Error'>
                        <h1 className={`${className}__title`}>Something went wrong</h1>
                        <div className={`${className}__texts`}>
                            <p className={`${className}__text`}>Unfortunately, an error has occured.</p>
                            <p className={`${className}__text`}>You can try to reload the page to fix the problem.</p>
                            <p className={`${className}__text`}>If that doesn't work, you can join the Discord server where you'll find more help.</p>
                        </div>
                        <Button
                            color='discord'
                            href='https://discord.gg/F6eaxK88sx'
                            iconName='discord'
                            label='Discord'
                            target='_blank'
                            tooltip='Join Discord server'
                            tooltipPosition='center'
                        />
                    </Group>
                    <Accordion title='More Information'>
                        <p className={`${className}__message`}>App version: {version}</p>
                        <p className={`${className}__message`}>{error.message}</p>
                        <pre className={`${className}__stack`}>{error.stack}</pre>
                    </Accordion>
                </div>
            </Page>
        );
    }
}