import merge from 'lodash/merge';
import { colors } from './colors';
import { TPuzzleColorScheme, TPuzzleName, TStore, TStoreSettings, TStoreSettingsPuzzles, TStoreSettingsTrainer, TStoreTrainer, TStoreTrainerCase, TTrainer, TTrainerCase, TTrainerCaseStatus, TTrainerCaseTimePenaly } from './types';

const defaultStoreSettingsTrainer: TStoreSettingsTrainer = {
    randomizeCaseOrder: false,
    hideName: false,
    hidePreview: false,
    hideSetup: false,
    hideSolutions: false,
    hideTimerValue: false,
    keepTimesOpen: false
};

const defaultStoreSettingsPuzzles: TStoreSettingsPuzzles = {
    '2x2x2': {
        colorNeutral: false,
        colorScheme: {
            '--color-2x2x2-up': colors.yellow,
            '--color-2x2x2-down': colors.white,
            '--color-2x2x2-front': colors.blue,
            '--color-2x2x2-back': colors.green,
            '--color-2x2x2-left': colors.orange,
            '--color-2x2x2-right': colors.red
        }
    },
    '3x3x3': {
        colorNeutral: false,
        colorScheme: {
            '--color-3x3x3-up': colors.yellow,
            '--color-3x3x3-down': colors.white,
            '--color-3x3x3-front': colors.blue,
            '--color-3x3x3-back': colors.green,
            '--color-3x3x3-left': colors.orange,
            '--color-3x3x3-right': colors.red
        }
    }
};

const defaultStoreSettings: TStoreSettings = {
    trainer: defaultStoreSettingsTrainer,
    puzzles: defaultStoreSettingsPuzzles
};

const defaultStore: TStore = {
    version: 1,
    settings: defaultStoreSettings,
    trainers: {}
};

const defaultStoreTrainer: TStoreTrainer = {
    cases: {}
};

const defaultStoreTrainerCase: TStoreTrainerCase = {
    status: 0,
    selected: true
};

class StoreManager {
    debug: boolean;
    store: TStore;

    constructor () {
        // Get store from local storage
        const localStorageItem = localStorage.getItem('store');

        // If store exists
        if (localStorageItem) {
            this.setStore(JSON.parse(localStorageItem));
        } else {
            console.log('No store found, creating default.');
            // Create default store
            this.setStore(defaultStore);
        }

        // Set debug flag
        this.debug = false;

        if (this.debug) {
            console.log(this.store);
        }
    }

    private setStoreInLocalStorage () {
        if (!this.store) {
            throw new Error('No store.');
        }

        // Set store in local storage
        localStorage.setItem('store', JSON.stringify(this.store));
    }

    // ----- Store -----
    setStore (newStore: TStore) {
        if (!newStore) {
            throw new Error('No new store.');
        }

        this.store = structuredClone(newStore);

        if (this.debug) {
            console.log(this.store);
        }

        this.setStoreInLocalStorage();
    }

    resetStore () {
        this.setStore(defaultStore);
    }

    // ----- Settings -----
    getSettings () {
        // Get store settings and add default properties
        const storeSettings: TStoreSettings = merge({}, defaultStoreSettings, this.store.settings);

        return storeSettings;
    }

    getColorScheme (puzzleName: TPuzzleName) {
        if (!puzzleName) {
            throw new Error('Missing puzzle name.');
        }

        return merge({}, defaultStoreSettings.puzzles[puzzleName].colorScheme, this.store.settings.puzzles[puzzleName].colorScheme);
    }

    setTrainerSetting (settingName: keyof TStoreSettingsTrainer, value: unknown) {
        if (!settingName) {
            throw new Error('Missing setting name.');
        }

        if (typeof(value) === 'undefined') {
            throw new Error('Missing value.');
        }

        // Create store with updated values
        const storeUpdate = {
            settings: {
                trainer: {
                    [settingName]: value
                }
            }
        };

        // Merge current and new store
        const newStore = merge({}, this.store, storeUpdate);

        // Update store
        this.setStore(newStore);
    }

    setPuzzleSetting (puzzleName: TPuzzleName, settingName: keyof TStoreSettingsPuzzles[TPuzzleName], value: unknown) {
        if (!puzzleName) {
            throw new Error('Missing puzzle name.');
        }

        if (!settingName) {
            throw new Error('Missing setting name.');
        }

        if (typeof(value) === 'undefined') {
            throw new Error('Missing value.');
        }

        // Create store with updated values
        const storeUpdate = {
            settings: {
                puzzles: {
                    [puzzleName]: {
                        [settingName]: value
                    }
                }
            }
        };

        // Merge current and new store
        const newStore = merge({}, this.store, storeUpdate);

        // Update store
        this.setStore(newStore);
    }

    setColorScheme (puzzleName: TPuzzleName, value: TPuzzleColorScheme) {
        if (!puzzleName) {
            throw new Error('Missing puzzle name.');
        }

        if (typeof(value) === 'undefined') {
            throw new Error('Missing value.');
        }

        // Create store with updated values
        const storeUpdate = {
            settings: {
                puzzles: {
                    [puzzleName]: {
                        colorScheme: value
                    }
                }
            }
        };

        // Merge current and new store
        const newStore = merge({}, this.store, storeUpdate);

        // Update store
        this.setStore(newStore);
    }

    resetColorScheme (puzzleName: TPuzzleName) {
        if (!puzzleName) {
            throw new Error('Missing puzzle name.');
        }

        // Create store with updated values
        const storeUpdate = {
            settings: {
                puzzles: {
                    [puzzleName]: {
                        colorScheme: defaultStoreSettingsPuzzles[puzzleName].colorScheme
                    }
                }
            }
        };

        // Merge current and new store
        const newStore = merge({}, this.store, storeUpdate);

        // Update store
        this.setStore(newStore);
    }

    // ----- Trainers -----
    getTrainer (puzzleName: TPuzzleName, trainerId: string, casesId: string[]) {
        if (!puzzleName) {
            throw new Error('Missing puzzle name.');
        }

        if (!trainerId) {
            throw new Error('Missing trainer id.');
        }

        if (!casesId) {
            throw new Error('Missing cases id.');
        }

        // Get store trainer and add default properties
        const storeTrainer: TTrainer = merge({}, defaultStoreTrainer, this.store.trainers?.[puzzleName]?.[trainerId]);

        // For each case id
        casesId.forEach((caseId) => {
            // Add default properties to case
            storeTrainer.cases[caseId] = merge({}, defaultStoreTrainerCase, storeTrainer.cases[caseId]);
        });

        return storeTrainer;
    }

    getCase (puzzleName: TPuzzleName, trainerId: string, caseId: string) {
        if (!puzzleName) {
            throw new Error('Missing puzzle name.');
        }

        if (!trainerId) {
            throw new Error('Missing trainer id.');
        }

        if (!caseId) {
            throw new Error('Missing case id.');
        }

        // Get store trainer case and add default properties
        const storeTrainerCase: TTrainerCase = merge({}, defaultStoreTrainerCase, this.store.trainers?.[puzzleName]?.[trainerId]?.cases[caseId]);

        return storeTrainerCase;
    }

    setCaseFavoriteSolution (puzzleName: TPuzzleName, trainerId: string, caseId: string, favoriteSolution = '') {
        if (!puzzleName) {
            throw new Error('Missing puzzle name.');
        }

        if (!trainerId) {
            throw new Error('Missing trainer id.');
        }

        if (!caseId) {
            throw new Error('Missing case id.');
        }

        // Create store with updated values
        const storeUpdate = {
            trainers: {
                [puzzleName]: {
                    [trainerId]: {
                        cases: {
                            [caseId]: {
                                favoriteSolution
                            }
                        }
                    }
                }
            }
        };

        // Merge current and new store
        const newStore = merge({}, this.store, storeUpdate);

        // Update store
        this.setStore(newStore);
    }

    setCaseStatus (puzzleName: TPuzzleName, trainerId: string, caseId: string, status: TTrainerCaseStatus) {
        if (!puzzleName) {
            throw new Error('Missing puzzle name.');
        }

        if (!trainerId) {
            throw new Error('Missing trainer id.');
        }

        if (!caseId) {
            throw new Error('Missing case id.');
        }

        if (typeof(status) !== 'number') {
            throw new Error('Missing status.');
        }

        // Create store with updated values
        const storeUpdate = {
            trainers: {
                [puzzleName]: {
                    [trainerId]: {
                        cases: {
                            [caseId]: {
                                status
                            }
                        }
                    }
                }
            }
        };

        // Merge current and new store
        const newStore = merge({}, this.store, storeUpdate);

        // Update store
        this.setStore(newStore);
    }

    setCasesStatus (puzzleName: TPuzzleName, trainerId: string, casesId: string[], status: TTrainerCaseStatus) {
        if (!puzzleName) {
            throw new Error('Missing puzzle name.');
        }

        if (!trainerId) {
            throw new Error('Missing trainer id.');
        }

        if (!casesId) {
            throw new Error('Missing cases id.');
        }

        if (typeof(status) !== 'number') {
            throw new Error('Missing status.');
        }

        const cases = {};

        // Set status for each case
        casesId.forEach((caseId) => {
            cases[caseId] = {
                status
            };
        });

        // Create store with updated values
        const storeUpdate = {
            trainers: {
                [puzzleName]: {
                    [trainerId]: {
                        cases
                    }
                }
            }
        };

        // Merge current and new store
        const newStore = merge({}, this.store, storeUpdate);

        // Update store
        this.setStore(newStore);
    }

    setCaseSelected (puzzleName: TPuzzleName, trainerId: string, caseId: string, selected: boolean) {
        if (!puzzleName) {
            throw new Error('Missing puzzle name.');
        }

        if (!trainerId) {
            throw new Error('Missing trainer id.');
        }

        if (!caseId) {
            throw new Error('Missing case id.');
        }

        if (typeof(selected) !== 'boolean') {
            throw new Error('Missing selected.');
        }

        // Create store with updated values
        const storeUpdate = {
            trainers: {
                [puzzleName]: {
                    [trainerId]: {
                        cases: {
                            [caseId]: {
                                selected
                            }
                        }
                    }
                }
            }
        };

        // Merge current and new store
        const newStore = merge({}, this.store, storeUpdate);

        // Update store
        this.setStore(newStore);
    }

    setCasesSelected (puzzleName: TPuzzleName, trainerId: string, casesId: string[], selected: boolean) {
        if (!puzzleName) {
            throw new Error('Missing puzzle name.');
        }

        if (!trainerId) {
            throw new Error('Missing trainer id.');
        }

        if (!casesId) {
            throw new Error('Missing cases id.');
        }

        if (typeof(selected) !== 'boolean') {
            throw new Error('Missing selected.');
        }

        const cases = {};

        // Set selected value for each case
        casesId.forEach((caseId) => {
            cases[caseId] = {
                selected
            };
        });

        // Create store with updated values
        const storeUpdate = {
            trainers: {
                [puzzleName]: {
                    [trainerId]: {
                        cases
                    }
                }
            }
        };

        // Merge current and new store
        const newStore = merge({}, this.store, storeUpdate);

        // Update store
        this.setStore(newStore);
    }

    addCaseTime (puzzleName: TPuzzleName, trainerId: string, caseId: string, milliseconds: number) {
        const { store } = this;

        if (!puzzleName) {
            throw new Error('Missing puzzle name.');
        }

        if (!trainerId) {
            throw new Error('Missing trainer id.');
        }

        if (!caseId) {
            throw new Error('Missing case id.');
        }

        if (typeof milliseconds !== 'number') {
            throw new Error('Missing milliseconds.');
        }

        const newStore: TStore = {
            ...store,
            trainers: {
                ...store.trainers,
                [puzzleName]: {
                    ...store.trainers[puzzleName],
                    [trainerId]: {
                        ...store.trainers[puzzleName]?.[trainerId],
                        cases: {
                            ...store.trainers[puzzleName]?.[trainerId].cases,
                            [caseId]: {
                                ...store.trainers[puzzleName]?.[trainerId].cases[caseId],
                                times: [
                                    ...store.trainers[puzzleName]?.[trainerId].cases[caseId]?.times || [],
                                    {
                                        milliseconds,
                                        penalty: null,
                                        timestamp: Date.now()
                                    }
                                ]
                            }
                        }
                    }
                }
            }
        };

        // Update store
        this.setStore(newStore);
    }

    setCaseTimePenalty (puzzleName: TPuzzleName, trainerId: string, caseId: string, index: number, penalty: TTrainerCaseTimePenaly) {
        const { store } = this;

        if (!puzzleName) {
            throw new Error('Missing puzzle name.');
        }

        if (!trainerId) {
            throw new Error('Missing trainer id.');
        }

        if (!caseId) {
            throw new Error('Missing case id.');
        }

        if (typeof index !== 'number') {
            throw new Error('Missing time index.');
        }

        if (penalty !== null && typeof penalty !== 'string') {
            throw new Error('Missing penalty.');
        }

        const newStore = structuredClone(store);

        if (!newStore.trainers[puzzleName]) {
            throw new Error('Missing puzzle.');
        }

        if (!newStore.trainers[puzzleName][trainerId].cases[caseId]) {
            throw new Error('Missing case.');
        }

        if (!newStore.trainers[puzzleName][trainerId].cases[caseId].times) {
            throw new Error('Missing times.');
        }

        if (penalty === newStore.trainers[puzzleName][trainerId].cases[caseId].times[index].penalty) {
            return;
        }

        newStore.trainers[puzzleName][trainerId].cases[caseId].times[index].penalty = penalty;

        // Update store
        this.setStore(newStore);
    }

    removeCaseTime (puzzleName: TPuzzleName, trainerId: string, caseId: string, index: number) {
        const { store } = this;

        if (!puzzleName) {
            throw new Error('Missing puzzle name.');
        }

        if (!trainerId) {
            throw new Error('Missing trainer id.');
        }

        if (!caseId) {
            throw new Error('Missing case id.');
        }

        if (typeof index !== 'number') {
            throw new Error('Missing time index.');
        }

        const newStore = structuredClone(store);

        if (!newStore.trainers[puzzleName]) {
            throw new Error('Missing puzzle.');
        }

        if (!newStore.trainers[puzzleName][trainerId].cases[caseId]) {
            throw new Error('Missing case.');
        }

        if (!newStore.trainers[puzzleName][trainerId].cases[caseId].times) {
            throw new Error('Missing times.');
        }

        if (!newStore.trainers[puzzleName][trainerId].cases[caseId].times[index]) {
            throw new Error('Missing time.');
        }

        newStore.trainers[puzzleName][trainerId].cases[caseId].times.splice(index, 1);

        // Update store
        this.setStore(newStore);
    }

    removeAllCaseTimes (puzzleName: TPuzzleName, trainerId: string, caseId: string) {
        const { store } = this;

        if (!puzzleName) {
            throw new Error('Missing puzzle name.');
        }

        if (!trainerId) {
            throw new Error('Missing trainer id.');
        }

        if (!caseId) {
            throw new Error('Missing case id.');
        }

        const newStore = structuredClone(store);

        if (!newStore.trainers[puzzleName]) {
            throw new Error('Missing puzzle.');
        }

        if (!newStore.trainers[puzzleName][trainerId].cases[caseId]) {
            throw new Error('Missing case.');
        }

        if (!newStore.trainers[puzzleName][trainerId].cases[caseId].times) {
            throw new Error('Missing times.');
        }

        newStore.trainers[puzzleName][trainerId].cases[caseId].times = [];

        // Update store
        this.setStore(newStore);
    }
}

export const storeManager = new StoreManager();