// https://fonts.google.com/icons?icon.style=Rounded&icon.set=Material+Symbols

import React from 'react';

type TProps = {
    ariaLabel?: string;
    name: string;
    parentClass: string;
};

export function Icon ({ ariaLabel, name, parentClass }: TProps) {
    const className = `icon ${parentClass}`;

    return (
        <span
            aria-hidden={ariaLabel ? false : true}
            aria-label={ariaLabel}
            className={`${className} icon--${name}`}
            role={ariaLabel && 'img'}
        ></span>
    );
}