import React, { ReactNode } from 'react';

type TProps = {
    alignment?: 'left' | 'center' | 'right';
    ariaLive?: boolean;
    anchorId?: string;
    children?: ReactNode;
    fullWidth?: boolean;
    title?: string;
    tooltip?: string;
};

export function Group ({
    alignment,
    ariaLive,
    anchorId,
    children,
    fullWidth = true,
    title,
    tooltip
}: TProps) {
    const className = 'group';
    const classNames = [className];

    if (alignment) {
        classNames.push(`${className}--alignment-${alignment}`);
    }

    if (fullWidth) {
        classNames.push(`${className}--full-width`);
    }

    const titleElement = title && (
        <h2
            aria-label={tooltip}
            className={`${className}__title`}
            data-tooltip={tooltip}
            id={anchorId}
            tabIndex={tooltip ? 0 : undefined}
        >
            <span className={`${className}__title-text`}>{title}</span>
        </h2>
    );

    return (
        <div
            className={classNames.join(' ')}
            {...(ariaLive && { 'aria-live': 'polite', role: 'region' })}
        >
            {titleElement}
            <div className={`${className}__content`}>
                {children}
            </div>
        </div>
    );
}