import { TPuzzleColorScheme, TPuzzleImage, TPuzzleName } from '../types';
import { getColorNeutralColorSchemeNxNxN } from './NxNxN';

export const puzzleNames: TPuzzleName[] = ['2x2x2', '3x3x3'];

export const puzzleImages: { [key in TPuzzleName]: TPuzzleImage[] } = {
    '2x2x2': [
        'one-face',
        'three-faces'
    ],
    '3x3x3': [
        'one-face',
        'three-faces'
    ]
};

/** Get color neutral custom properties for a puzzle. */
export function getColorNeutralColorScheme (puzzleName: TPuzzleName) {
    let colorScheme: TPuzzleColorScheme;

    switch (puzzleName) {
        case '2x2x2':
        case '3x3x3':
            colorScheme = getColorNeutralColorSchemeNxNxN(puzzleName);
            break;
    }

    return colorScheme;
}