import { useEffect, useRef } from 'react';

/** Custom hook to add an event listener to a target. */
export function useEventListener (target: any, eventName: string, handler: (event: any) => unknown, options?: AddEventListenerOptions ) {
    // Create a reference to the handler that will be updated
    const handlerRef = useRef(handler);

    useEffect(() => {
        // Update handler reference after each render
        handlerRef.current = handler;
    });

    useEffect(() => {
        // Create a wrapper to call the current handler
        function currentHandler (event: Event) {
            return handlerRef.current(event);
        }

        // Add event listener to the target
        target.addEventListener(eventName, currentHandler, options);

        return () => {
            // Remove event listener from the target
            target.removeEventListener(eventName, currentHandler, options);
        };
    }, [target, eventName, options]);
}

/** Custom hook to access the previous value of a variable. */
export function usePrevious (value?: any) {
    // Create a reference to the value that will be updated
    const valueRef = useRef(value);

    // Store current value in ref
    useEffect(() => {
        // Update value reference after each render
        valueRef.current = value;
    }, [value]);

    // Return previous value (before render)
    return valueRef.current;
}