import { storeManager } from '../StoreManager';
import { TPuzzleName } from '../types';
import { getRandomItemFromArray } from '../utils';

// Permutations to get all 24 possible puzzle orientations
const colorPermutationArrays = [
    // [u, d, f, b, l, r]
    [0, 1, 2, 3, 4, 5],
    [0, 1, 5, 4, 2, 3], // y
    [0, 1, 3, 2, 5, 4], // y y
    [0, 1, 4, 5, 3, 2], // y y y
    [2, 3, 1, 0, 4, 5], // x
    [2, 3, 5, 4, 1, 0], // x y
    [2, 3, 0, 1, 5, 4], // x y y
    [2, 3, 4, 5, 0, 1], // x y y y
    [1, 0, 3, 2, 4, 5], // x x
    [1, 0, 5, 4, 3, 2], // x x y
    [1, 0, 2, 3, 5, 4], // x x y y
    [1, 0, 4, 5, 2, 3], // x x y y y
    [3, 2, 0, 1, 4, 5], // x x x
    [3, 2, 5, 4, 0, 1], // x x x y
    [3, 2, 1, 0, 5, 4], // x x x y y
    [3, 2, 4, 5, 1, 0], // x x x y y y
    [4, 5, 2, 3, 1, 0], // z
    [4, 5, 0, 1, 2, 3], // z y
    [4, 5, 3, 2, 0, 1], // z y y
    [4, 5, 1, 0, 3, 2], // z y y y
    [5, 4, 2, 3, 0, 1], // z z z
    [5, 4, 1, 0, 2, 3], // z z z y
    [5, 4, 3, 2, 1, 0], // z z z y y
    [5, 4, 0, 1, 3, 2]  // z z z y y y
];

/** Get color neutral NxNxN color scheme. */
export function getColorNeutralColorSchemeNxNxN (puzzleName: TPuzzleName)  {
    const colorScheme = storeManager.getColorScheme(puzzleName);

    // Get current colors
    const colors = [
        colorScheme[`--color-${puzzleName}-up`],
        colorScheme[`--color-${puzzleName}-down`],
        colorScheme[`--color-${puzzleName}-front`],
        colorScheme[`--color-${puzzleName}-back`],
        colorScheme[`--color-${puzzleName}-left`],
        colorScheme[`--color-${puzzleName}-right`]
    ];

    // Get random permutation color array
    const randomColorPermutationArray = getRandomItemFromArray(colorPermutationArrays) as number[];

    // Get new colors using permutation array
    const newColors = [
        colors[randomColorPermutationArray[0]],
        colors[randomColorPermutationArray[1]],
        colors[randomColorPermutationArray[2]],
        colors[randomColorPermutationArray[3]],
        colors[randomColorPermutationArray[4]],
        colors[randomColorPermutationArray[5]]
    ];

    // Return new color scheme
    return {
        [`--color-${puzzleName}-up`]: newColors[0],
        [`--color-${puzzleName}-down`]: newColors[1],
        [`--color-${puzzleName}-front`]: newColors[2],
        [`--color-${puzzleName}-back`]: newColors[3],
        [`--color-${puzzleName}-left`]: newColors[4],
        [`--color-${puzzleName}-right`]: newColors[5]
    };
}